TeamStandings.getInitialProps = async function (context) {
  const resNHL = await fetch('https://statsapi.web.nhl.com/api/v1/teams/')
  const nhlData = await resNHL.json();

  const nhlTeams = nhlData.teams;
  // console.log(nhlTeams);
  return {
    teams: nhlTeams
  }
}
